:root {
  --lime:hsl(61, 70%, 52%);
  --lime-opacity:hsla(61, 70%, 52%, 0.2);
  --red:hsl(4, 69%, 50%);
  --white:hsl(0, 0%, 100%);
  --background:hsl(202, 86%, 94%);
  --border:hsl(203, 41%, 72%);
  --clearBtn:hsl(200, 26%, 54%);
  --text-label:hsl(200, 24%, 40%);
  --text-color:hsl(202, 55%, 16%);
  --result: hsl(202, 56%, 12%);
}

:root {
  --Plus-Jakarta-Sans:"Plus Jakarta Sans", serif;
  --500:500;
  --700:700;
}

/* 1. Use a more-intuitive box-sizing model */
*, *::before, *::after {
  box-sizing: border-box;
}

/* 2. Remove default margin */
* {
  margin: 0;
}

/* 3. Enable keyword animations */
@media (prefers-reduced-motion: no-preference) {
  html {
    interpolate-size: allow-keywords;
  }
}
body {
  /* 4. Add accessible line-height */
  line-height: 1.5;
  /* 5. Improve text rendering */
  -webkit-font-smoothing: antialiased;
}

/* 6. Improve media defaults */
img, picture, video, canvas, svg {
  display: block;
  max-width: 100%;
}

/* 7. Inherit fonts for form controls */
input, button, textarea, select {
  font: inherit;
}

/* 8. Avoid text overflows */
p, h1, h2, h3, h4, h5, h6 {
  overflow-wrap: break-word;
}

/* 9. Improve line wrapping */
p {
  text-wrap: pretty;
}

h1, h2, h3, h4, h5, h6 {
  text-wrap: balance;
}

/*
  10. Create a root stacking context
*/
#root, #__next {
  isolation: isolate;
}

/* Chrome, Safari, Edge, Opera */
input[type=number]::-webkit-outer-spin-button,
input[type=number]::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

/* Firefox */
input[type=number] {
  appearance: textfield;
  -webkit-appearance: none;
  -moz-appearance: textfield;
}

input:focus {
  outline: none;
  box-shadow: none;
  cursor: none;
}

body {
  background-color: var(--background);
  font-family: var(--Plus-Jakarta-Sans);
  font-size: 1rem;
  width: 100%;
  height: 100vh;
  display: grid;
  place-items: center;
}

.wrapper {
  background-color: var(--white);
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
@media (min-width: 40em) {
  .wrapper {
    flex-direction: row;
    max-width: 62.5rem;
    max-height: 34.375rem;
    border-radius: 1.5rem;
    overflow: hidden;
  }
}

.information {
  padding: 1rem;
  display: flex;
  flex-direction: column;
  align-content: center;
  justify-content: center;
  gap: 1rem;
  height: 100%;
  width: 100%;
}
@media (min-width: 40em) {
  .information__head {
    display: flex;
    justify-content: space-between;
  }
}
.information__title {
  align-self: start;
}
.information__clearBtn {
  border: none;
  background-color: var(--white);
  color: var(--clearBtn);
  text-decoration: underline;
  font-size: 1rem;
}
.information__wrapper {
  width: 100%;
}
@media (min-width: 40em) {
  .information__wrapper {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    gap: 1rem;
  }
}

.mortgage {
  display: flex;
  flex-direction: column;
  align-items: start;
  justify-content: center;
  gap: 1rem;
}
.mortgage__label {
  color: var(--text-label);
  width: 100%;
  height: 100%;
}
.mortgage__div {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  border: 0.0625rem solid var(--clearBtn);
  border-radius: 0.3rem;
  overflow: hidden;
  margin-top: 0.5rem;
}
.mortgage__icon {
  background-color: var(--background);
  padding: 0.5rem 1rem;
  font-weight: var(--700);
}
.mortgage__input {
  border: none;
  width: 100%;
  height: 100%;
  padding: 0 0.5rem;
}
.mortgage__error {
  display: none;
  color: var(--red);
}
.mortgage__typeForm {
  display: flex;
  flex-direction: column;
  align-items: start;
  justify-content: start;
  width: 100%;
  gap: 0.5rem;
}
.mortgage__typeInfo {
  color: var(--text-label);
}
.mortgage__typeDiv {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: start;
  gap: 1rem;
  padding: 1rem;
  font-size: 1.1rem;
  font-weight: var(--700);
  width: 100%;
  border: 0.0625rem solid var(--clearBtn);
  border-radius: 0.5rem;
}
.mortgage__typeLabel {
  width: 100%;
  height: 100%;
}
.mortgage__calculateBtn {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  background-color: var(--lime);
  border: none;
  padding: 1rem;
  border-radius: 2rem;
  transition: background-color 0.3s ease, transform 0.2s ease;
}

.information__clearBtn:is(:hover, :focus) {
  color: var(--text-color);
  cursor: pointer;
}

.mortgage__div:hover, .mortgage__div:focus-within {
  border: 0.125rem solid var(--text-color);
}

.mortgage__typeDiv:hover, .mortgage__typeDiv:focus-within {
  border: 0.125rem solid var(--lime);
  background-color: var(--lime-opacity);
}

.mortgage__calculateBtn:is(:hover, :focus) {
  background-color: var(--lime-opacity);
}

.empty {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: var(--text-color);
  color: var(--white);
  gap: 1rem;
  padding: 1.5rem;
  width: 100%;
  height: 100%;
}
@media (min-width: 40em) {
  .empty {
    border-bottom-left-radius: 5rem;
  }
}
.empty__title {
  width: 100%;
  text-align: center;
}
.empty__hint {
  width: 100%;
  text-align: center;
  color: var(--border);
}

.complete {
  background-color: var(--text-color);
  color: var(--white);
  display: none;
  flex-direction: column;
  align-items: start;
  justify-content: center;
  width: 100%;
  height: 100%;
  padding: 1.5rem;
  gap: 1.5rem;
}
@media (min-width: 40em) {
  .complete {
    border-bottom-left-radius: 5rem;
  }
}
.complete__comment {
  color: var(--border);
}
.complete__result {
  background-color: var(--result);
  border-top: 0.125rem solid var(--lime);
  width: 100%;
  border-radius: 0.5rem;
  padding: 1rem;
}
.complete__repayments {
  padding-bottom: 1rem;
}
.complete__label {
  color: var(--border);
}
.complete__amount {
  font-size: 2rem;
  font-weight: var(--700);
  color: var(--lime);
}
.complete hr {
  opacity: 0.3;
}
.complete__total {
  padding: 1rem 0;
}
.complete__totalAmount {
  font-size: 1.4375rem;
  font-weight: var(--700);
}