@use 'util' as *;
.wrapper{
    background-color: var(--white);
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
     @include breakpoint(medium) {
        flex-direction: row;
        max-width: rem(1000);
        max-height:rem(550) ;
        border-radius: 1.5rem;
        overflow: hidden;
     }

}